.popup__input {
  width: 100%;
  height: 47px;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-size: 14px;
  line-height: 17px;
  box-sizing: border-box;
  padding: 5px 6px 13px;
  margin-bottom: 24px;
}

.popup__input:last-of-type {
  margin-bottom: 0;
}

.error-message {
  color: #FF0000;
  font-size: 12px;
  line-height: 15px;
}
